body {
	font-family: Avenir, sans-serif;
	font-weight: 400;
	line-height: 1.4;
	color: $dark; }

html {
	-ms-overflow-style: none;
	scrollbar-width: none;

	&::-webkit-scrollbar {
		width: 0;
		background: transparent; } }

.out {
	@include r(767) {
		overflow: hidden;

		.menu:not(:first-child) {
			display: none; }

		.sidebar--content {
			height: auto !important; } } }

.container {
	max-width: 1600px;
	width: 100%;
	margin: 0 auto;
	padding: 0 40px;

	@include r(767) {
		padding: 0 20px; } }

.mainWrapper {
	position: relative; }

button {
	position: relative;
	background-color: $white;
	border: 1px solid $dark;
	height: 44px;
	text-align: center;
	text-transform: uppercase;
	color: $dark;
	font-size: 12px;
	transform: translate(0, 0);
	transition: transform 0.3s ease-in-out;

	&:hover {
		@include rmin(768) {
			transform: translate(-4px, 4px); } }

	&:focus,
	&:active {
		@include rmin(768) {
			color: $white;
			background-color: $dark; }

		@include r(767) {
			animation-duration: 0.3s;
			animation-name: action; } } }

.anchor {
  color: black;
  text-decoration: none;

  &:hover {
    text-decoration: underline; } }

@keyframes action {
	0%, 100% {
		color: $dark; }

	to {
		color: $white;
		background-color: $dark; } }

.buttonWrapper {
	position: relative;
	border: 2px solid $dark;
	background-color: transparent; }
