    bottom: auto;
    top: 100px;
    left: 0;
    padding-left: 20px;
    width: 100vw;
    height: 100vh;
    margin-left: 0;
  }
}

.menu__list--isActive {
  transform: translateX(0);
}

.menu__item {
  overflow: hidden;
  will-change: transform;
  transition: transform 500ms cubic-bezier(0.645, 0.045, 0.355, 1);
}

.menu__item::before {
  content: '';
  position: absolute;
  left: 5px;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: 30px;
  height: 10px;
  background-position: center;
  background-image: url(../img/arrow-active.svg);
  transform: translate(-40px, -5px);
  transition: transform 500ms cubic-bezier(0.645, 0.045, 0.355, 1);
}

.menu__item--isActive {
  position: relative;
}

.menu__item--isActive::before {
  content: '';
  position: absolute;
  left: 5px;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: 30px;
  height: 10px;
  background-position: center;
  background-image: url(../img/arrow-active.svg);
  transform: translate(-3px, -5px);
}

@media only screen and (max-width: 767px) {
  .menu__item--isActive::before {
    content: none;
  }
